import React, { Component } from 'react';

const DataInt = (props) => {
	return(
		<div className="di">
			<div className="nombre text-info">{props.data.apellido} {props.data.nombre}</div>
			<div className="telefono">Cel: {props.data.caracteristica} - {props.data.telefono}</div>
			<div className="mail">Email: {props.data.mail}</div>
		</div>
	);
}


class Comision extends Component {
	constructor(props) {
		super(props);
		this.state = {
			loading: true,
			idComision: 0,
			data_comision: {},
			data_integrantes: [],
			total_integrantes: 0
		}
	}

	componentWillMount() {
		this.setState({
			idComision: this.props.idComision 
		}, () => {
			//Obtener el nombre de la Comisión
			var comision = new Promise((resolve, reject) => {
				fetch(`${process.env.REACT_APP_API_HOST}/comision/${this.state.idComision}`, {
					method: 'GET',
					headers: {
						"Authorization": "asdssffsdff",
						"Content-Type": "application/json"
					},
				})
				.then(res => res.json())
				.then((result) => {
					if(!result.err) {
						this.setState({
							data_comision: result.data.registros[0],
							total_integrantes: result.data.count
						}, () => {
							resolve("Ok");
						});
					}
				}, (error) => { //???
					reject(error);
				});
			});
			//Obtener los Integrantes de la Comisión
			var miembros = new Promise((resolve, reject) => {
				fetch(`${process.env.REACT_APP_API_HOST}/integrantes/comision/${this.state.idComision}`, {
					method: 'GET',
					headers: {
						"Authorization": "asdssffsdff",
						"Content-Type": "application/json"
					},
				})
				.then(res => res.json())
				.then((result) => {
					if(!result.err) {
						this.setState({
							data_integrantes: result.data.registros
						}, () => {
							resolve("Ok");
						});
					}
				}, (error) => { //???
					reject(error);
				});
			});
			Promise.all([comision, miembros]).then(values => { 
				this.setState({
					loading: false
				});
			});
		});
	}

	render() {
		const integrantes = this.state.data_integrantes.map((integrante) => {
			return(
				<li className="list-group-item" key={`int-${integrante.id}`}>
					<DataInt data={integrante} />
				</li>
			);
		});
		return (
			<div className="row justify-content-center mt-3">
				<div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
					{
						this.state.loading ?
						<div>Cargando...</div>
						:
						<div className="bg-white text-dark rounded p-3">
							<div className="text-center bg-dark text-white p-2"><h5>{this.state.data_comision.nombre}</h5></div>
							<div className="mb-3 mt-3">Integrantes:</div>
							<ul className="list-group">
								{integrantes}
							</ul>
							<div className="mt-3 mb-3">Total de {this.state.total_integrantes} integrantes.</div>
						</div>
					}
				</div>
			</div>
		);
	}
}

export default Comision;