import React, { Component } from 'react';
//import logo from './logo.svg';
//import './App.css';

import Navbar from "./components/Navbar";
import Pie from "./components/Pie";

import DniForm from "./components/DniForm";
import RegistroForm from "./components/RegistroForm";
import Comision from "./components/Comision";

class App extends Component {
	constructor(props) {
		super(props);
		this.state = {
			encontrado: false,
			registrarse: false,
			dni: "",
			integrante: {}
		}
		this.handleRegistro = this.handleRegistro.bind(this);
		this.handleCancelForm = this.handleCancelForm.bind(this);
		this.handleOkForm = this.handleOkForm.bind(this);
		this.handleOkDni = this.handleOkDni.bind(this);
	}

	handleOkDni(data) {
		this.setState({
			integrante: data,
			encontrado: true
		});
	}

	handleRegistro() {
		this.setState({
			registrarse: true
		});
	}

	handleCancelForm() {
		this.setState({
			registrarse: false
		});
	}

	handleOkForm(id) {
		fetch(`${process.env.REACT_APP_API_HOST}/integrantes/id/${id}`, {
			method: 'GET',
			headers: {
				"Authorization": "asdssffsdff",
				"Content-Type": "application/json"
			},
		})
		.then(res => res.json())
		.then((result) => {
			if(!result.err) {
				if(parseInt(result.data.count) > 0) {
					this.setState({
						registrarse: false,
						integrante: result.data.registros[0],
						encontrado: true

					});
				} else {
					console.log("Error!");
				}
			} else {
				console.log("Error!");
			}
		}, (error) => { //???
			console.log("Error!");
		});
	}

	render() {
		const { encontrado, registrarse } = this.state;
		return(
			<React.Fragment>
				<Navbar />
				{
					encontrado ?
					<React.Fragment>
						<div className="container">
							<div className="row justify-content-center">
								<div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
									<div className="bg-white text-dark rounded p-3"><h5>Bienvenido <span className="text-primary">{this.state.integrante.apellido} {this.state.integrante.nombre}</span></h5></div>
								</div>
							</div>
							<div className="row justify-content-center mt-3">
								<div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
									<div className="bg-white text-dark p-3 rounded d-flex flex-column">
										<h5>Últimos Mensajes:</h5>
										<div className="alert alert-dark" role="alert">No hay mensajes.</div>
									</div>
								</div>
							</div>
							<Comision idComision={this.state.integrante.idcomision} />
						</div>
					</React.Fragment>
					:
					<React.Fragment>
						{
							registrarse ?
							<RegistroForm onCancel={this.handleCancelForm} onOk={this.handleOkForm} />
							:
							<React.Fragment>
								<DniForm onOkDni={this.handleOkDni} />
								<div className="container mt-3">
									<div className="row justify-content-center">
										<div className="col-sm-12 col-md-4 d-flex justify-content-center">
											<button className="btn btn-dark" onClick={this.handleRegistro}>Quiero sumarme al equipo!</button>
										</div>
									</div>
								</div>
							</React.Fragment>
						}
					</React.Fragment>
				}
				<Pie />
			</React.Fragment>
		);
	}
}

